import { Injectable } from '@angular/core';

import { DOMService } from './dom.service';

import { OffsetPosition as Offset } from '@progress/kendo-popup-common';

import { AlignElementSettings } from '../models/align-element-settings.interface';

import { eitherRect } from '../util';

/**
 * @hidden
 */
@Injectable()
export class AlignService {
    constructor(private _dom: DOMService) { }

    public alignElement(settings: AlignElementSettings): Offset {
        const { anchor, element, anchorAlign, elementAlign, offset } = settings;

        return this._dom.align({
            anchorAlign: anchorAlign,
            anchorRect: eitherRect(this._dom.position(anchor), offset),
            elementAlign: elementAlign,
            elementRect: this._dom.position(element)
        });
    }
}



